import React from 'react'
import Footer from '../footer/Footer';

import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import Carousel from 'react-bootstrap/Carousel';
const Cochabamba = () => {
  const videoId = 'RyyWzN1aet8';
  return (
    <div>
          <div style={{ height: '100%', overflow: 'hidden' }}>
          <Container fluid className="h-100">
            <Row className="h-100">
              <Col xs={12} md={8} lg={6} className="mx-auto my-auto">
                <Carousel className="w-100 h-100"> 
                  <Carousel.Item>
                    <img src={"./imagenes/cochabamba1.jpg"} alt="Plato Paceño" className="img-fluid h-100 w-100" />
                  </Carousel.Item>
                  <Carousel.Item>
                    <img src={"./imagenes/cochabamba2.jpg"} alt="Plato Paceño" className="img-fluid h-100 w-100" />
                  </Carousel.Item>
                  <Carousel.Item>
                    <img src={"./imagenes/cochabamba3.jpg"} alt="Plato Paceño" className="img-fluid h-100 w-100" />
                  </Carousel.Item>
                </Carousel>
              </Col>
            </Row>
          </Container>
        </div>


        <h1 className='text-white p-2 bg-dark'>COCHABAMBA</h1>
            <blockquote className='blockquote'>
                <p className='text.center mt-3'>
                Cochabamba posee una gran variedad de platos típicos, muchos de ellos datan de épocas ancestrales. Otros son relativamente nuevos pero han calado hondo en el gusto de los cochabambinos. Al estar la ciudad en un valle fértil, su suelo privilegiado hace de Cochabamba la Capital Gastronómica de Bolivia. Entre los platos típicos de esta región tenemos  al Pampaku, silpancho cochabambino y otros.
                </p>
            </blockquote>
            <h1 className='text-white p-2 bg-dark'>SILPANCHO COCHABAMBINO</h1>
            <img src="./imagenes/InicioCochabama.jpg" width= '80%' height= '315' alt="Plato Paceño" fluid />
            <p className='text.center mt-3'>
                    <h2>Ingredientes</h2>
                    <h3>1 papa grande</h3>
                    <h3>1/2 taza de aceite vegetal</h3>
                    <h3>2 tazas de arroz blanco cocido</h3>
                    <h3>2 bistecs de carne blanda sin grasa ni nervios (corte delgado como para milanesas)</h3>
                    <h3>1/2 taza de pan rallado</h3>
                    <h3>1 taza de tomates en dados pequeños</h3>
                    <h3>1 cebolla blanca, mediana, cortada en dados pequeños</h3>
                    <h3>1 cucharada de perejil picado</h3>
                    <h3>2 huevos</h3>

                    <h1>Paso a paso</h1>

                    <h3>Paso 1
                    Lava bien la papa y quítale la piel con un pelador de papas. Colócala en una olla con suficiente agua y agrega un punto de sal. Hiérvela durante unos 20 minutos, hasta que esté cocida, pero firme. Luego colócala en el refrigerador para que se enfríe, mientras preparas lo demás.</h3>
                    <h3>Paso 2
                    Cocina el arroz con tu método favorito, lo importante es que –una vez cocido- completes dos tazas. Si lo deseas, puedes tenerlo preparado desde el día anterior.</h3>
                    <h3>Paso 3
                    Adoba los bistecs con el adobo. En un plato plano, extiende el pan rallado. Coloca 1/4 de taza de aceite en una sartén mediana, a fuego medio. Pasa los bistecs por el pan rallado, presionando cada lado hasta cubrirlos uniformemente. Cuando el aceite esté caliente, cocínalos durante 1 1/2 minutos de cada lado, o hasta que los extremos estén dorados. Ten cuidado con cocinarlos demasiado, ya que el pan rallado se dora con facilidad.</h3>
                    <h3>Paso 4
                    Cuando estén listos, pásalos por una servilleta de papel para quitar el exceso de grasa. Mantenlos tibios.</h3>
                    <h3>Paso 5
                    Lava y pica la cebolla en cubos pequeños y déjala a un lado. Abre la lata de tomates, escúrrelos en un colador y calcula 1 taza.</h3>
                    <h3>Paso 6 
Saca la papa del refrigerador y pícala en rodajas de 1/2 pulgada, aproximadamente. En un sartén anti-adherente, coloca el otro 1/4 de taza de aceite, a calentar a fuego medio alto. Cuando el aceite esté caliente, coloca las papas y fríelas durante aproximadamente dos minutos de cada lado, o hasta que estén doradas. Colócalas sobre servilletas de papel para absorber el aceite.</h3>
                    <h3>Paso 7 Mientras fríes las papas, rocía con aceite en spray una sartén pequeña, y fríe los huevos a tu gusto, condimentando con sal y pimienta.</h3>
                    <h3>Una vez listas las papas, el arroz, los bistecs, y los huevos, procede a armar el Silpancho.</h3>
                    <h3>Coloca una taza de arroz extendida sobre un plato, luego el bistec, y luego el huevo frito. Alrededor, coloca los tomates y las cebollas en cubos y de un lado, agrega 3 a 4 rodajas de papas. Esparce perejil picado en cada plato y ¡buen provecho!</h3>
            </p>
            
            <h1 className='text-white p-2 bg-dark'>VIDEO PREPARACION</h1>
            <Container>
                    <Row>
                        <Col md={6} className="mx-auto mt-5">
                            <div className="embed-responsive embed-responsive-16by9">
                                <iframe
                                    width='80%'
                                    height='315'
                                    title="YouTube Video"
                                    className="embed-responsive-item"
                                    src={`https://www.youtube.com/embed/${videoId}`}
                                    allowFullScreen />
                            </div>
                        </Col>
                    </Row>
            </Container>

        <Footer/>
    </div>
  )
}

export default Cochabamba