import React from 'react'
import Footer from '../footer/Footer';


import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import Carousel from 'react-bootstrap/Carousel';


const inicio = () => {
  return (
    <div>
        <div style={{ height: '100%', overflow: 'hidden' }}>
          <Container fluid className="h-100">
            <Row className="h-100">
              <Col xs={12} md={8} lg={6} className="mx-auto my-auto">
                <Carousel className="w-100 h-100"> 
                  <Carousel.Item>
                    <img src={"./plato paceño.jpg"} alt="Plato Paceño" className="img-fluid h-100 w-100" />
                  </Carousel.Item>
                  <Carousel.Item>
                    <img src={"./imagenes/IncioTarija.jpg"} alt="Plato Paceño" className="img-fluid h-100 w-100" />
                  </Carousel.Item>
                  <Carousel.Item>
                    <img src={"./imagenes/InicioBeni.jpg"} alt="Plato Paceño" className="img-fluid h-100 w-100" />
                  </Carousel.Item>
                  <Carousel.Item>
                    <img src={"./imagenes/InicioCochabama.jpg"} alt="Plato Paceño" className="img-fluid h-100 w-100" />
                  </Carousel.Item>
                  <Carousel.Item>
                    <img src={"./imagenes/InicioPando.jpg"} alt="Plato Paceño" className="img-fluid h-100 w-100" />
                  </Carousel.Item>
                  <Carousel.Item>
                    <img src={"./imagenes/InicioPotosi.jpg"} alt="Plato Paceño" className="img-fluid h-100 w-100" />
                  </Carousel.Item>
                  <Carousel.Item>
                    <img src={"./imagenes/IniciOruro.jpg"} alt="Plato Paceño" className="img-fluid h-100 w-100" />
                  </Carousel.Item>
                  <Carousel.Item>
                    <img src={"./imagenes/InicioSantaCruz.jpg"} alt="Plato Paceño" className="img-fluid h-100 w-100" />
                  </Carousel.Item>
                  <Carousel.Item>
                    <img src={"./imagenes/InicioSucre.jpg"} alt="Plato Paceño" className="img-fluid h-100 w-100" />
                  </Carousel.Item>
                  
                </Carousel>
              </Col>
            </Row>
          </Container>
        </div>
        <h1 className='text-white p-2 bg-dark'>PLATOS TIPICOS DE BOLIVIA</h1>

        <p className='text.center mt-3'>Probablemente no sepas qué comer en Bolivia, puesto que quizás este país sudamericano es de los menos conocidos. No obstante, la gastronomía boliviana es muy variada, y no sólo eso, la calidad de la carne y las patatas es muy superior a la de otros muchos países.</p>
        <p className='text.center mt-3'>Para iniciarte en la comida de Bolivia, lo primero que tienes que saber es que en este país se cultivan más de doscientas variedades de patatas. La riqueza de los platos típicos de Bolivia es tan extensa que no podríamos abarcar todos sus platos. Toma nota de algunos de los más típicos y sabrosos que encontrarás durante tu viaje a Bolivia.</p>

        
        <Footer/>
    </div>

  )
}

export default inicio