import React from 'react'
import Footer from '../footer/Footer';

import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import Carousel from 'react-bootstrap/Carousel';

const Oruro = () => {
  const videoId = 'ezJ0zDVzyHA';
  return (
    
    <div>
        <div style={{ height: '100%', overflow: 'hidden' }}>
          <Container fluid className="h-100">
            <Row className="h-100">
              <Col xs={12} md={8} lg={6} className="mx-auto my-auto">
                <Carousel className="w-100 h-100"> 
                  <Carousel.Item>
                    <img src={"./imagenes/oruro1.jpg"} alt="Plato Paceño" className="img-fluid h-100 w-100" />
                  </Carousel.Item>
                  <Carousel.Item>
                    <img src={"./imagenes/oruro2.jpeg"} alt="Plato Paceño" className="img-fluid h-100 w-100" />
                  </Carousel.Item>
                  <Carousel.Item>
                    <img src={"./imagenes/oruro3.jpg"} alt="Plato Paceño" className="img-fluid h-100 w-100" />
                  </Carousel.Item>
                </Carousel>
              </Col>
            </Row>
          </Container>
        </div>
        <h1 className='text-white p-2 bg-dark'>ORURO</h1>
            <blockquote className='blockquote'>
                <p className='text.center mt-3'>
                En el departamento de Oruro la característica de tradición culinaria de esta región, es que usan carne de res, oveja y llama; acompañado de tubérculos producidos en esta parte del altiplano. Los platos típicos son: Rostro Asado, charquekan y otros.
                </p>
            </blockquote>
            <h1 className='text-white p-2 bg-dark'>CHARQUEKAN</h1>
            <img src="./imagenes/IniciOruro.jpg" width= '80%' height= '315' alt="Plato Paceño" fluid />
            <p className='text.center mt-3'>
                    <h2>Ingredientes</h2>
                    <h3>1 kilo de carne deshidratada de llama o res charque</h3>
                    <h3>1/2 kilo de maiz blanco</h3>
                    <h3>12 papas medianas</h3>
                    <h3>12 huevos</h3>
                    <h3>6 quesos</h3>
                    <h3>limón opcional</h3>

                    <h1>Paso a paso</h1>

                    <h3>Paso 1Primero preparar el maíz, hacer remojar noche antes y hacer cocer con abundante agua y sal, luego reservar.
</h3>
                    <h3>Paso 2
                    Lavar las papas y hacerlas cocer juntos con los huevos</h3>
                    <h3>Paso 3
                    Hacer cocer en agua la carne deshidratada, para que se le salga toda la sal que contiene por el proceso de secado, luego de que haya cocido unos 20 minutos retirarla del fuego, escurrir y deshilar.</h3>
                    <h3>Paso 4 En una sartén poner aceite y poner a retostar los hilos de carne hasta que esten crujientes.
</h3>
                    <h3>Paso 5
                    Servir sobre una cama de mote (maíz cocido), papas, huevos y adornar con queso.</h3>
            </p>
            
            <h1 className='text-white p-2 bg-dark'>VIDEO PREPARACION</h1>
            <Container>
                    <Row>
                        <Col md={6} className="mx-auto mt-5">
                            <div className="embed-responsive embed-responsive-16by9">
                                <iframe
                                    width='80%'
                                    height='315'
                                    title="YouTube Video"
                                    className="embed-responsive-item"
                                    src={`https://www.youtube.com/embed/${videoId}`}
                                    allowFullScreen />
                            </div>
                        </Col>
                    </Row>
            </Container>


        <Footer/>
    </div>
  )
}

export default Oruro